#reconnection > div.reconnection {
  position: fixed;
  top: 50%;
  left: 50%;
  background: #fff;
  border: #e8e8e8 1px solid;
  border-radius: 3px;
  width: 260px;
  height: 260px;
  margin-top: -130px;
  margin-left: -130px;
  z-index: 9999;
}
#reconnection > div.reconnection > p {
  text-align: center;
  color: #cd3d3d;
  line-height: 30px;
  margin-top: 110px;
}
#reconnection .spinner {
  margin: 50px 0 0 37%;
  width: 32px;
  height: 32px;
  position: relative;
}
#reconnection .cube1,
#reconnection .cube2 {
  background: #cd3d3d url(assets/images/icon1.png) no-repeat center;
  background-size: 88%;
  width: 30px;
  height: 30px;
  border-radius: 2px;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: cubemove 2.4s infinite ease-in-out;
  animation: cubemove 2.4s infinite ease-in-out;
}
#reconnection .cube2 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
@-webkit-keyframes cubemove {
25% {
    -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
    opacity: 0.5;
}
50% {
    -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
    opacity: 1;
}
75% {
    -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
    opacity: 0.5;
}
100% {
    -webkit-transform: rotate(-360deg);
    opacity: 1;
}
}
@keyframes cubemove {
25% {
    transform: translateX(42px) rotate(-90deg) scale(0.5);
    -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
    opacity: 0.5;
}
50% {
    transform: translateX(42px) translateY(42px) rotate(-179deg);
    -webkit-transform: translateX(42px) translateY(42px) rotate(-179deg);
    opacity: 1;
}
50.1% {
    transform: translateX(42px) translateY(42px) rotate(-180deg);
    -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
    opacity: 1;
}
75% {
    transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
    -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
    opacity: 0.5;
}
100% {
    transform: rotate(-360deg);
    -webkit-transform: rotate(-360deg);
    opacity: 1;
}
}
.mask-canvas-backdrop {
  position: fixed;
  z-index: 998;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  transition: background 350ms cubic-bezier(0.165, 0.84, 0.44, 1);
  background: rgba(0, 0, 0, 0.4);
  box-shadow: -4px 0 4px rgba(0, 0, 0, 0.5), 4px 0 4px rgba(0, 0, 0, 0.5);
  -webkit-tap-highlight-color: transparent;
}
.createRoom {
  position: absolute;
  top: 50%;
  left: 15%;
  width: 70%;
  margin-top: -150px;
  z-index: 999;
}
.createRoom .mui-input-group {
  border: #ddd 1px solid;
  border-radius: 5px;
  padding: 15px 0;
}
.createRoom .mui-input-group .mui-button-row {
  margin-top: 15px;
}
.createRoom .mui-input-group:before,
.createRoom .mui-input-group:after {
  display: none;
}
.createRoom input[type="text"] {
  font-size: 14px;
  padding-bottom: 13px;
}
html,
body {
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background: #fff;
}
.child-view {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  transition: all 0.6s cubic-bezier(0.55, 0, 0.1, 1);
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(-80px, 0);
  transform: translate(-80px, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}
/*同级页面动画*/
.slide-fade-enter-active {
  transition: all .3s ease;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .5s ease;
  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  -ms-transition: all .5s ease;
  -o-transition: all .5s ease;
}
.slide-fade-enter {
  transform: translateX(20px);
  -webkit-transform: translateX(20px);
  -moz-transform: translateX(20px);
  -ms-transform: translateX(20px);
  -o-transform: translateX(20px);
  opacity: 0;
}
.slide-fade-leave-active {
  opacity: 0;
}



